* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.list-container li {
  /* border: 1px solid red; */
  list-style: none;
  padding: 10px;
  font-size: 1.2rem;
  text-transform: capitalize;
  border-radius: 10px;
  margin-top: 10px;

  background-color: #fefefe;
  box-shadow: rgba(17, 12, 46, 0.15) 0px 48px 100px 0px;
}
.list-container {
  width: 100%;
  margin: auto;
  margin-top: 40px;
  padding: 20px;
}
.main {
  background-color: grey;
  width: 80%;
  margin: auto;
}
